<template>
	<section class="login-box" v-bind:style="{'backgroundImage': 'url('+LOGINBACKGROUND+')'}">
        <div class="login-header">
            <div class="header-con">
                <div class="logo">中云支付管理平台</div>
                <div class="about">
                    <span>欢迎来到中云支付管理平台</span>|<a href="javascript:void(0)">帮助中心</a>
                </div>
            </div>
        </div>
        <div class="login-con">
            <div class="login-form">
                <div class="form-con">
                    <div class="title">密码加密</div>
                    <div class="tips"><span v-if="isError">用户名或密码不正确</span></div>
                    <ul class="form-li">
                    	 <li>
                            <span class="iconfont icon-account"></span>
                            <p><input name="" type="text"  v-model="i" placeholder="类型" class="input-text-01"></p>
                        </li>
                        <li>
                            <span class="iconfont icon-user"></span>
                            <p><input name="" type="text" placeholder="用户名" v-model="username" class="input-text-01"></p>
                        </li>
                        <li>
                            <span class="iconfont icon-unlock-alt"></span>
                            <p><input name="" type="text" placeholder="密码" v-model="password" class="input-text-01"></p>
                        </li>
                        <li>
                            <span class="iconfont icon-unlock-alt"></span>
                            <p><input name="" type="text" placeholder="加密后密码" v-model="passwordEncry" class="input-text-01" readonly="readonly"></p>
                        </li>
                    </ul>
                    
                    <button class="login-submint" @click="submintEncry">加密</button>
                    
                </div>
            </div>
        </div>
    </section>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      LOGINBACKGROUND: './static/img/login_bg.jpg',
      isError: false,
      username: '',
      password: '',
      passwordEncry:'',
      i:1
    }
  },methods: {
  	
  	// 点击加密
      submintEncry: function() {
      	var _this = this;
          this.$ajax.get('/user/passWord', {
    			params: {	
    						i:_this.i,
      						loginName:_this.username,
      						password:_this.password,
    				    }
  				})
          .then(function (response) {
          		console.log(response);
          		_this.passwordEncry=response.data;
          })
          .catch(function (error) {
              console.log(error);
          });
      }
  	
  },created: function() {
	
  }
 }
</script>

<style>
.login-box{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    overflow: hidden;
}
.login-header{
    width: 100%;
    height: 50px;
    background: rgba(0, 0, 0, 0.5);
    color: #ccc;
}
.login-header>.header-con{
    height: 50px;
    line-height: 50px;
    margin: 0 20px;
}
.login-header .logo{
    float: left;
    width: 210px;
    height: 50px;
}
.login-header .about{
    height: 50px;
    margin: 0 0 0 210px;
    text-align: right;
}
.login-header .about>span{
    display: inline-block;
    margin: 0 10px;
}
.login-header .about>a{
    display: inline-block;
    margin: 0 10px;
    color: #ccc;
}
.login-con{
    position: relative;
    /* max-width: 1000px; */
    height: calc(100% - 50px);
    margin: 0 1150px;
}
.login-con>.login-form{
    position: absolute;
    top: 14%;
    right: 40px;
    width: 350px;
    height: 400px;
    padding: 10px;
    background: rgba(0, 0, 0, 0.3);
    /* border-radius: 4px; */
}
.login-con>.login-form .form-con {
    height: 100%;
    background: #fff;
    overflow: hidden;
}
.login-con>.login-form .title {
    height: 20px;
    line-height: 20px;
    margin: 20px 0 0;
    text-align: center;
    font-size: 16px;
}
.login-con>.login-form .tips {
    height: 16px;
    line-height: 16px;
    margin: 10px 0 0;
    text-align: center;
    color: #ff2400;
}
.login-con>.login-form .form-li{
    width: 290px;
    margin: 0 auto;
    overflow: hidden;
}
.login-con>.login-form .form-li>li{
    height: 40px;
    margin: 14px 0 0;
    border: 1px #dbdbdb solid;
}
.login-con>.login-form .form-li>li span{
    float: left;
    width: 50px;
    height: 38px;
    line-height: 38px;
    text-align: center;
    font-size: 18px;
    color: #999;
}
.login-con>.login-form .form-li>li span::before{
    display: inline-block;
    margin: -3px 0 0;
    vertical-align: middle;
}
.login-con>.login-form .form-li>li p{
    height: 38px;
    margin: 0 0 0 50px;
    padding: 4px 5px 4px 0;
}
.login-con>.login-form .form-li>li h2{
    float: left;
    width: 130px;
    height: 38px;
    padding: 4px 10px;
}
.login-con>.login-form .form-li>li h3{
    position: relative;
    height: 38px;
    margin: 0 0 0 130px;
}
.login-con>.login-form .form-li>li h3::before{
    content: '';
    position: absolute;
    top: 7px;
    left: 0;
    width: 1px;
    height: 25px;
    background: #dbdbdb;
}
.login-con>.login-form .form-li>li h3 img{
    float: left;
    width: 80px;
    height: 30px;
    margin: 4px 0 0 10px;
}
.login-con>.login-form .form-li>li h3 a{
    float: right;
    width: 60px;
    height: 30px;
    line-height: 30px;
    margin: 4px 0 0 0;
    text-align: center;
    font-size: 12px;
}
.input-text-01{
    width: 100%;
    height: 30px;
    border: none;
    font-size: 14px;
}
.login-submint{
    display: block;
    width: 290px;
    height: 40px;
    margin: 30px auto 0;
    background: #4299f4;
    font-size: 16px;
    color: #fff;
    cursor: pointer;
}
.login-submint:hover{
    background: #328ce9;
}
.forget-psw{
  width: 290px;
  line-height: 20px;
  margin: 10px auto 0;
  text-align: right;
}
.forget-psw>a{
  display: inline-block;
}
.forget-psw>a:hover{
  color: #4299f4;
 }
</style>


